ul.events-list {
	width: 100%;
	list-style: none;
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 20px;
	margin-bottom: 30px;
}

ul.events-list li {
	display: flex;
	flex-direction: column;
}

ul.events-list li header {
	width: 100%;
	height: 240px;
	background-size: contain;
	background-repeat: no-repeat;
	border-radius: 4px;
}

ul.events-list li strong {
	margin-top: 12px;
	font-size: 24px;
	color: #444;
}

div.filter-panel {
	display: flex;
	justify-content: space-between;
	margin-bottom: 50px;
}

ul.events-list header div {
	display: flex;
	flex-direction: row-reverse;
}

ul.notification {
	list-style: none;
	margin-bottom: 16px;
	display: flex;
	justify-content: space-between;
}
ul.notification li {
	font-size: 16px;
	line-height: 24px;
}

@media (max-width: 768px) {
	ul.events-list {
		grid-template-columns: 1fr;
	}
}
