@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  min-width: 100%;
}

body {
  background: rgba(0,0,0,1) url(/static/media/bg2.6d55a120.jpg) no-repeat;
  background-size: cover;
  -webkit-font-smoothing: antialiased !important;
}

body,
input,
button {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 14px;
}

.container {
  margin: 40px auto;
  max-width: 450px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container h1 {
  color: #f0585b;
  font-size: 36px;
}

.content{
  width: 95%;
  opacity: 0.8;
  background: #ffffff;
  margin-top: 30px;
  border-radius: 4px;
  padding: 30px;
}

.content > p {
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 30px;
}
.content button.submit-btn {
  border: 0;
  border-radius: 4px;
  width: 100%;
  height: 42px;
  padding: 0 20px;
  font-size: 16px;
  font-weight: 700;
  background: #f0585b;
  color: #ffffff;
  cursor: pointer;
  margin-top: 10px;
}

.content button.submit-btn:hover {
  background: #e14f50;
  opacity: 1;
}

.content button.Secondary-btn {
  border: 0;
  border-radius: 4px;
  width: 100%;
  height: 42px;
  padding: 0 20px;
  font-size: 16px;
  font-weight: 700;
  background: #337ab7 ;
  color: #ffffff;
  cursor: pointer;
}
.content button.Secondary-btn:hover {
  background: #286090;
  opacity: 1;
}

@media (max-width: 700px){
  body {
    height: 100%;
  }
  .content{
    width: 90%;
    opacity: 0.8;
    background: #ffffff;
    margin-top: 50px;
    border-radius: 4px;
    padding: 30px;
  }
}
ul.events-list {
	width: 100%;
	list-style: none;
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 20px;
	margin-bottom: 30px;
}

ul.events-list li {
	display: flex;
	flex-direction: column;
}

ul.events-list li header {
	width: 100%;
	height: 240px;
	background-size: contain;
	background-repeat: no-repeat;
	border-radius: 4px;
}

ul.events-list li strong {
	margin-top: 12px;
	font-size: 24px;
	color: #444;
}

div.filter-panel {
	display: flex;
	justify-content: space-between;
	margin-bottom: 50px;
}

ul.events-list header div {
	display: flex;
	flex-direction: row-reverse;
}

ul.notification {
	list-style: none;
	margin-bottom: 16px;
	display: flex;
	justify-content: space-between;
}
ul.notification li {
	font-size: 16px;
	line-height: 24px;
}

@media (max-width: 768px) {
	ul.events-list {
		grid-template-columns: 1fr;
	}
}

label#thumbnail {
  margin-bottom: 20px;
  border: 1px;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 120px;
}

label#thumbnail input {
  display: none;
}

label#thumbnail.has-thumbnail {
  border: 0;
}

label#thumbnail.has-thumbnail img {
  display: none;
}

.container > .event-validation {
  margin-top: 12px;
}
.events {
  list-style: none;
  display: flex;
  flex-direction: column;
}

.events li {
  padding: 30px;
  margin: 1rem;
  border-radius: 4px;
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.events div {
  margin-top: 0.5rem;
}

.events-details span {
  margin: 0.5rem;
}

.Approved {
  color: green;
}

.Rejected {
  color: red;
}

.Pending {
  color: blue;
}
